<template>
  <div>
    <a-alert
      message="เข้าห้องประชุม"
      description="โปรดกดปุ่มด้านล่าง เพื่อทำการสแกน QR code เพื่อเข้าห้องประชุม"
      type="info"
      show-icon
    />
    <a-spin :spinning="spinning">
      <div class="spin-content">
        <v-row class="mb-3" align="center" justify="center">
          <v-col class="mt-7" align="center">
            <v-img
              max-height="280px"
              max-width="280px"
              src="../../assets/img/test.png"
            >
              <v-img
                class="mt-8"
                v-show="this.state_qrcode == 0"
                max-height="215px"
                max-width="230px"
                src="../../assets/img/Qrcode-center.png"
              >
              </v-img>

              <div class="position_camera" v-if="this.state_qrcode == 1">
                <qrcode-drop-zone @decode="onDecode" @init="logErrors">
                  <qrcode-stream
                    @decode="onDecode"
                    @detect="onDetect"
                    @init="onInit"
                  />
                </qrcode-drop-zone>

                <qrcode-capture v-if="noStreamApiSupport" @decode="onDecode" />
              </div>
            </v-img>
          </v-col>
        </v-row>
      </div>
    </a-spin>

    <v-row class="mt-5 pb-5" align="center" justify="center">
      <v-col align="center">
        <a-button
          type="primary"
          icon="qrcode"
          size="large"
          style="height:80px;"
          @click="Open_Change_state_qrcode()"
        >
          กดเพื่อสแกนเข้าห้องประชุม
        </a-button>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  components: {},

  data() {
    return {
      spinning: false,
      noStreamApiSupport: false,
      state_qrcode: 0,
      room: "",
      email: null,
      result_api: {},
      dialog: false,
    };
  },
  created() {
    this.getdata_storage();
  },
  methods: {
    onDecode(room) {
      this.room = room;
      console.log(this.room);
      console.log(this.email);
      if (this.room != "") {
        this.spinning = !this.spinning;
        this.API_Qrcode();
      }
    },
    API_Qrcode() {
      // console.log('API_Qrcode');
      // console.log(response.data);
      this.spinning = true;
      if (this.room == null || this.email == null) {
        this.$message.error("กรุุณาแสดงคิวอาร์โค้ด");
      } else {
        this.result_api = { one_email: this.email, room_num: this.room };
      }
      this.axios({
        method: "POST",
        url:
          this.$store.state.url_get_API_axios +
          "api/v1/unlock/" +
          "9JTNyITNxcDO4ITM2EjOiUGdhR2XkVmcpBHelJCLiEjZ2UmNlNWNxM2NzcDMjhjZxYTNlZjN0QjYmNTM3QzNVJiOiQWayV2c1JyeSUJ72",
        data: this.result_api,
        // headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          this.results = response.data;
          console.log(response.data);
          // console.log(response.data.Value.result[0].covid_level);
          // console.log(response.data.Value.result[0].door_action);

          if (response.data.Message == "Meeting Room Accessed") {
            this.$success({
              title: "unlock success",
              content: "Unlock Success",
              centered: true,
            });
            // this.List_booking_all = this.results;
            console.log(response.data);
          } else if (response.data.Message == "Cannot Access Meeting Room") {
            this.$error({
              title: "ไม่สามารถเข้าห้องประชุมได้ ",
              content: "ไม่สามารถเข้าห้องประชุมได้ ",
            });
            // console.log(response.data);
          } else if (response.data.Message == "Main Door Accessed") {
            if (response.data.Value.result[0].door_action == "not_open" && response.data.Value.result[0].covid_level == "") {
              this.$error({
                title: "ห้ามเข้าพื้นที่",
                content: "สถานะของคุณคือ" + response.data.Value.help,
                centered: true,
              });
            }else if (
              response.data.Value.result[0].door_action == "not_open" &&
              response.data.Value.result[0].covid_level == "red"
            ) {
              this.$success({
                title: "ห้ามเข้าพื้นที่",
                content: "สถานะของคุณคือ แดง " + "กรุณาติดต่อเจ้าหน้าที่เพื่อกักตัว",
                centered: true,
              });
            }
             else if (
              response.data.Value.result[0].door_action == "open" &&
              response.data.Value.result[0].covid_level == "green"
            ) {
              this.$success({
                title: "เปิดประตูสำเร็จ",
                content: "สถานะของคุณคือ เขียว " + response.data.Value.help,
                centered: true,
              });
            } 
            else if (
              response.data.Value.result[0].door_action == "open" &&
              response.data.Value.result[0].covid_level == "yellow"
            ) {
              this.$success({
                title: "เปิดประตูสำเร็จ",
                content: "สถานะของคุณคือ เหลือง " + response.data.Value.help,
                centered: true,
              });
            }
            else if (
              response.data.Value.result[0].door_action == "open" &&
              response.data.Value.result[0].covid_level == "orange"
            ) {
              this.$success({
                title: "เปิดประตูสำเร็จ",
                content: "สถานะของคุณคือ ส้ม " + response.data.Value.help,
                centered: true,
              });
            }
          } else if (response.data.Message == "Cannot Access Main Door") {
            this.$error({
              title: response.data.Value.result.door_action,
              content: response.data.Value.help,
              centered: true,
            });
          } else {
            // this.spinning = false;
            this.$error({
              title: "ไม่พบข้อมูล",
              content: "ไม่พบข้อมูล",
            });
          }
        })
        .catch((error) => {
          // this.spinning = false;
          console.log(error.message);
        });
    },
    getdata_storage() {
      let data = JSON.parse(window.localStorage.getItem("user_data"));

      if (data === null) {
        this.getdata = null;
      } else {
        this.nickname = data.nickname;
        this.email = data.email;
        this.profilepicture = data.profilepicture;
      }
    },
    cleanSpinning() {
      this.spinning = false;
    },
    logErrors(promise) {
      promise.catch(console.error);
    },
    async onDetect(promise) {
      try {
        const {
          imageData, // raw image data of image/frame
        } = await promise;

        console.log(imageData);
      } catch (error) {
        console.log(error);
      }
    },
    Open_Change_state_qrcode() {
      console.log(this.state_qrcode);
      this.state_qrcode = 1;
    },
    // Open_DialogShowStatus(){
    //   this.
    // },
    async onInit(promise) {
      try {
        await promise;
      } catch (error) {
        if (error.name === "StreamApiNotSupportedError") {
          this.noStreamApiSupport = true;
        }
      }
    },
  },
};
</script>

<style scoped>
.error {
  font-weight: bold;
  color: red;
}
.position_camera {
  left: 0;
  right: 0;
  padding-top: 1.5em;
  padding-bottom: 1em;
  padding-left: 2em;
  padding-right: 2em;
  height: 275px;
  z-index: 100;
  display: flex;

  justify-content: center;
}
</style>
