<template>
  <div class="home">
    <v-card class="mt-3" flat>
      <v-container class="pa-5">
        <v-row class="" align="center" justify="center">
          <v-col>
            <a-tabs
              default-active-key="1"
              @prevClick="callback"
              @nextClick="callback"
            >
              <a-tab-pane key="1" tab="เข้าห้องประชุม">
                <JoinCalendarMeeting
                  ref="JoinCalendarMeeting"
                ></JoinCalendarMeeting>
              </a-tab-pane>
              <a-tab-pane key="2" tab="จองห้องประชุม">
                <CalendarMeeting ref="CalendarMeeting" @testnon="test()"></CalendarMeeting>
              </a-tab-pane>
              <a-tab-pane key="3" tab="ตารางปฏิทินประชุม">
                <SchedulePerson ref="SchedulePerson"></SchedulePerson>
              </a-tab-pane>
            </a-tabs>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
    <Footer class="mt-3" ref="Footer"></Footer>
  </div>
</template>

<script>
import CalendarMeeting from "../components/CalendatMeeting/CalendarMeeting.vue";
import SchedulePerson from "../components/SchedulePerson/SchedulePerson.vue";
import JoinCalendarMeeting from "../components/JoinCalendarMeeting/JoinCalendarMeeting.vue";
import Footer from "./Footer.vue";

export default {
  name: "HomePage",
  components: { CalendarMeeting, SchedulePerson, JoinCalendarMeeting, Footer },
  data: () => ({
    loading: false,
  }),
  mounted() {
    var parameters = this.$route.query;
    // console.log(parameters);
    // console.log(this.parameters_expired_date);
    // console.log(this.parameters_onechat_token);

    this.parameters_expired_date = parameters.expired_date;
    this.parameters_onechat_token = parameters.onechat_token;
  },
  computed: {},
  created() {
    // this.API_GetUserLogin();
  },
  methods: {
    test(){console.log('i am homepage');},
    callback(val) {
      console.log(val);
    },

    API_GetUserLogin() {
      this.axios({
        method: "GET",
        url:
          this.$store.state.url_get_API_axios +
          "api/v1/getProfile/9BzM5MjMxcjM1ITM2EjOiUGdhR2XkVmcpBHelJCLigDZzcjY4AjN0MmMzMTMhljZ0QWNwQjNmFmZ0EDZwYTMVJiOiQWayV2c1Jye4fUzi"
        // headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          console.log(response.data);
          var results = response.data;

          if (response.data.Status == "success") {
            this.save_Data(results);
          } else if (response.data.Status == "fail, Token Invalid") {
            this.Model_countDown_error();
          } else {
            // this.spinning = false;

            this.$error({
              title: "ไม่พบข้อมูล",
              content: "ไม่พบข้อมูล",
            });
          }
        })
        .catch((error) => {
          // this.spinning = false;
          console.log(error.message);
          // this.loading = false;
          // if (error.message == "Request failed with status code 400") {
          //   this.$message.error("ไม่พบข้อมูล");
          // }
        });
    },
    Model_countDown_error() {
      let secondsToGo = 3;
      const modal = this.$error({
        title: "ไม่พบผู้ใช้นี้",
        content: `โปรดล็อกอินอีกครั้ง`,
        centered: true,
      });
      const interval = setInterval(() => {
        secondsToGo -= 1;
        modal.update({
          content: `โปรดล็อกอินอีกครั้ง`,
        });
      }, 1000);
      setTimeout(() => {
        clearInterval(interval);
        modal.destroy();
      }, secondsToGo * 1000);
    },
    save_Data(data) {
      var user_data = data.Value;
      window.localStorage.setItem("user_data", JSON.stringify(user_data));
    },
  },
};
</script>
<style lang="scss" scoped>
.truncatecus {
  max-width: 420px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
